.menu {
	z-index: 10;
	@include coverdiv;
	min-height: 100vh;
	background-color: #f53e4f;
	overflow-y: scroll;
	transition: ease-in-out 0.5s;
	padding: 0 40px;
	display: flex;
	align-items: center;
	transform: translateX(100%);
	@include xs {
		padding: 0 20px; }
	&.is-open {
		transform: translate(0); }
	&__content {
		max-width: 875px;
		color: #ffffff;
		text-align: center;
		padding: 120px 0;
		margin: auto;
		@include md {
			padding: 100px 0 50px; } }
	&__title {
		font-size: 73px;
		text-transform: uppercase;
		@include semi;
		margin-bottom: 79px;
		@include r(1100) {
			font-size: 63px; }
		@include md {
			font-size: 42px; }
		@include xs {
			font-size: 18px;
			margin-bottom: 70px;
			padding: 0 30px; } }
	&__download {
		font-size: 13px;
		margin-bottom: 59px;
		@include md {
			font-size: 11px; }
		.appstore-btn {
			margin: 0 auto;
			margin-bottom: 14px;
			@include md {
				height: 52px;
				width: 175px; } } }
	&__nav {
		margin-bottom: 22px; } }

